// owlbear-legacy/src/contexts/PermissoesContext.tsx
import React, { createContext, useContext, useState, useEffect } from "react";

interface Permissoes {
  pode_criar_conteudo: boolean;
  pode_editar_grid: boolean;
  pode_iniciar_sessao: boolean;
  pode_moderar_chat: boolean;
  pode_convidar_usuarios: boolean;
  is_mestre: boolean;
}

interface PermissoesContextType {
  permissoes: Permissoes | null;
  loading: boolean;
  canAddToken: () => boolean;
  canEditGrid: () => boolean;
  isMestre: () => boolean;
}

const PermissoesContext = createContext<PermissoesContextType>({
  permissoes: null,
  loading: true,
  canAddToken: () => false,
  canEditGrid: () => false,
  isMestre: () => false,
});

export function usePermissoes() {
  return useContext(PermissoesContext);
}

interface PermissoesProviderProps {
  sessionId: string;
  userId: string;
  children: React.ReactNode;
}

export function PermissoesProvider({ sessionId, userId, children }: PermissoesProviderProps) {
  const [permissoes, setPermissoes] = useState<Permissoes | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchPermissoes() {
      try {
        const apiBase = (window as any).OWLBEAR_CONFIG?.apiBase || '/api';
        const response = await fetch(`${apiBase}/permissoes/${sessionId}/${userId}`, {
          credentials: 'include'
        });
        
        if (response.ok) {
          const data = await response.json();
          setPermissoes(data);
          console.log(' Permisses carregadas:', data);
        } else {
          setPermissoes({
            pode_criar_conteudo: false,
            pode_editar_grid: false,
            pode_iniciar_sessao: false,
            pode_moderar_chat: false,
            pode_convidar_usuarios: false,
            is_mestre: false
          });
        }
      } catch (error) {
        console.error(' Erro ao carregar permisses:', error);
        setPermissoes({
          pode_criar_conteudo: false,
          pode_editar_grid: false,
          pode_iniciar_sessao: false,
          pode_moderar_chat: false,
          pode_convidar_usuarios: false,
          is_mestre: false
        });
      } finally {
        setLoading(false);
      }
    }

    if (sessionId && userId) {
      fetchPermissoes();
    }
  }, [sessionId, userId]);

  const canAddToken = () => {
    return permissoes?.pode_criar_conteudo ?? false;
  };

  const canEditGrid = () => {
    return permissoes?.pode_editar_grid ?? false;
  };

  const isMestre = () => {
    return permissoes?.is_mestre ?? false;
  };

  return (
    <PermissoesContext.Provider value={{ permissoes, loading, canAddToken, canEditGrid, isMestre }}>
      {children}
    </PermissoesContext.Provider>
  );
}